.description-input-wrapper {
    background-color: #EAC696; /* Set your desired background color */
    width: 100%; /* Occupy the full width of its parent */
}

.description-input-container {
    display: flex;
    justify-content: space-between;
    position: relative;
    top: -42px; /* Adjust the overlap as needed */
    margin-bottom: -20px; /* Offset to prevent content overlap */
    width: 95%; /* Set the width of the description component */
    margin: 0 auto; /* Center-align the description component */
}



.description-container,
.input-container {
    flex: 1;
    padding: 100px;
    border-radius: 8px;
}

.description-container {
    background-color: #102C57; /* Set your desired background color */
    color: white;
}
.description-container p,h2 {
    font-size: 22px; /* Adjust the font size as desired */
    line-height: 1.5; /* Adjust the line height for better spacing */
    font-family: "Arvo", serif;
}

.input-container {
    background-color: #f0f0f0; /* Set your desired background color */
}

.input-section {
    margin-bottom: 20px; /* Add margin bottom to create equal spacing */
}

.input-section h2 {
    margin-bottom: 10px;
}

.input-section input {
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    width: 150px;
}

/* Remaining styles remain unchanged */

.bhk-container {
    display: flex;
    flex-direction: column;
}

.bhk-container button {
    background-color: #007bff; /* Button color */
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 8px 12px;
    cursor: pointer;
    margin: 5px;
}

.bhk-container input[type="text"] {
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    width: 50px;
    text-align: center;
    margin: 5px;
}

.bhk-container input[type="range"] {
    width: 150px;
    margin: 5px;
}

.bhk-container span {
    font-weight: bold;
    margin: 5px;
}

.bath-container {
    display: flex;
    flex-direction: column;
}

.bath-container button {
    background-color: #007bff; /* Button color */
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 8px 12px;
    cursor: pointer;
    margin: 5px;
}

.bath-container input[type="text"] {
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    width: 50px;
    text-align: center;
    margin: 5px;
}

.bath-container input[type="range"] {
    width: 150px;
    margin: 5px;
}

.bath-container span {
    font-weight: bold;
    margin: 5px;
}
.location-container {
    margin-top: 20px; /* Adjust margin as needed */
}
  
.location-container h2 {
    font-size: 24px; /* Adjust font size as needed */
    margin-bottom: 10px; /* Adjust margin as needed */
}
  
.location {
    width: 100%; /* Adjust width as needed */
    padding: 10px; /* Adjust padding as needed */
    font-size: 16px; /* Adjust font size as needed */
    border-radius: 5px; /* Adjust border radius as needed */
    border: 1px solid #ccc; /* Adjust border style and color as needed */
}


.estimate-price-container {
    margin-top: 20px;
    text-align: center;
}

.submit {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    margin-right: 10px;
}

.submit:hover {
    background-color: #0056b3;
}

.result {
    margin-top: 18px;
    font-size: 13px;
    text-align: center;
    border: 1px solid #ced4da;
    padding: 5px;
    border-radius: 5px;
}
