.navbar {
 /* Background color for the navbar */
    background-size: 100% 100%; /* Resize the background image height to match the navbar height */
    background-repeat: no-repeat;
    background-position: center;
    padding: 80px; /* Adjust padding as needed */
    z-index: 1; /* Ensure navbar appears above other elements */
    text-align: center; /* Center-align the text */
  }
  
  .navbar-title {
    font-size: 50px;
    font-weight: bold;
    font-family: "Pacifico", cursive;
  }
  .material-symbols-outlined {
    font-variation-settings:
      'FILL' 0,
      'wght' 500,
      'GRAD' 0,
      'opsz' 24;
    font-size: 36px; 
}
